@font-face {
	font-family: 'lemonmilk-regular';
	src: url('../fonts/lemonmilk/lemonmilk-webfont.woff2') format('woff2'),
		 url('../fonts/lemonmilk/lemonmilk-webfont.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

