	font-style: normal;
}


/*
The following is based of Typebase:
https://github.com/devinhunt/typebase.css
I've edited it a bit, but it's a nice starting point.
*/


// font stacks
$sans-serif:				"Helvetica Neue", Helvetica, Arial, sans-serif;
$serif:						"Georgia", Cambria, Times New Roman, Times, serif;
$lato:						'Lato', $sans-serif;
$roboto:					'Roboto', $sans-serif;
$oswald: 					'Oswald', $sans-serif;
$lemonmilk-regular:			lemonmilk-regular, $lato;
$lemonmilk-regular-italic:	lemonmilk-regular__italic, $lato;
$lemonmilk-bold:			lemonmilk-bold, $lato;
$lemonmilk-bold__italic:	lemonmilk-bold__italic, $lato;
$lemonmilk-light:			lemonmilk-light, $lato;
$lemonmilk-light-italic:	lemonmilk-light__italic, $lato;

/*
some nice typographical defaults
more here: http://www.newnet-soft.com/blog/csstypography
*/
p {
  -ms-word-wrap: break-word;
  word-break: break-word; //non-standart css for webkit
  word-wrap: break-word;
  // cleaning up the ragged lines and breaks
  -webkit-hyphens: auto;
	 -moz-hyphens: auto;
		  hyphens: auto;
  // sets a minimum number of characters before and after the break
  -webkit-hyphenate-before: 2;
   -webkit-hyphenate-after: 3;
		   hyphenate-lines: 3;
  /* enabling fancy ligatures when available
  -webkit-font-feature-settings: "liga", "dlig";
   -moz-font-feature-settings: "liga=1, dlig=1";
	  -ms-font-feature-settings: "liga", "dlig";
	   -o-font-feature-settings: "liga", "dlig";
		  font-feature-settings: "liga", "dlig";
	*/
}

