/*********************
LAYOUT & GRID STYLES
*********************/

/*.wrap {
	width: 96%;
	margin: 0 auto;
}*/

.wrap{
  display:block;
  min-width:20em; // 320px
  width:20em;
  margin:0 auto;
  clear:both;
  padding:0 0.625rem;
  &:after{
	clear: both;
	content: " "; 
	display: block;
	font-size: 0;
	height: 0;
	visibility: hidden;
  }
  @include breakpoint(lgMobile) {
	width: 30em; // (480px)
  }
  @include breakpoint(tablet) {
	width: 48em; // (768px)
  }
  @include breakpoint(tabletUp) {
	width:100%;
	max-width: 75em;
  }
  @include breakpoint(desktop) {
	max-width: 75em; // (1200px)
	width:100%;
	padding:0;
  }
  @include breakpoint(smDesktop) {
	padding:0 0.625rem;
  }
}

.wrap-desktop{
  @include breakpoint(desktop) {
	position:relative;
	display:block;
	max-width:75em; // (1200px)
	width:100%;
	margin:0 auto;
	clear:both;
	&:after{
	  clear: both;
	  content: " ";
	  display: block;
	  font-size: 0;
	  height: 0;
	  visibility: hidden;
	}
  }
  @include breakpoint(smDesktop) {
	padding:0 0.625rem;
  }
}

.wrap-mobile{
  @include breakpoint(smMobile) {
	width:20em; // (320px)
	min-width:20em;
	padding:0 0.625rem;
	margin:0 auto;
	clear:both;
  }
  @include breakpoint(lgMobile) {
	width:30em; // (480px)
  }
  @include breakpoint(tablet) {
	width:48em; // (768px)
  }
}

/*********************
LINK STYLES
*********************/

a, a:visited {
	color: $link-color;
	transition:color ease 0.5s;

	/* on hover */
	&:hover, &:focus {
		color: $link-hover;
	}

	/* on click */
	&:active {

	}

	/* mobile tap color */
	&:link {
		/*
		this highlights links on iPhones/iPads.
		so it basically works like the :hover selector
		for mobile devices.
		*/
		-webkit-tap-highlight-color : rgba( 0, 0, 0, 0.3 );
	}
}

/******************************************************************
H1, H2, H3, H4, H5 STYLES
******************************************************************/

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
	text-rendering: optimizelegibility;
	font-weight: 500;
	font-family: $oswald;
	/*
	if you're going to use webfonts, be sure to check your weights
	http://css-tricks.com/watch-your-font-weight/
	*/

	/* removing text decoration from all headline links */
	a {
		text-decoration: none;
	}
}

h1, .h1 {
	font-size: 2.5em;
	line-height: 1.333em;
}

h2, .h2 {
	font-size: 1.5em;
	line-height: 1;
	margin: 0 0 0.375em;
}

h3, .h3 {
	font-size: 1.125em;
}

h4, .h4 {
	font-size: 1.1em;
}

h5, .h5 {
	font-size: 0.846em;
	line-height: 2.09em;
	text-transform: uppercase;
	letter-spacing: 2px;
}

hr{
	display: block;
	height: 1px;
	border: 0;
	border-top: 1px solid #999;
	margin: 1em 0;
	padding: 0; 
}

